export default {
    name: 'ContextMenu',
    props: {
        offset: {
            type: Object,
            default: () => {
                return {
                    clientX: 0,
                    clientY: 0
                }
            }
        },
        visible : {
            type :Boolean,
            default :false
        }
    },
    data() {
        return {
            left: 0,
            top: 0,
        }
    },
    watch: {
        offset: {
            handler: function () {
                const {
                    clientX,
                    clientY
                } = this.offset
                this.top = clientY;
                this.left = clientX;
            },
            deep: true
        },
        visible(value) {
            var that = this;
            if (value) {
                document.body.addEventListener('click', function(){
                    that.$emit('clearMenus')
                });
            } else {
                document.body.removeEventListener('click', function(){
                    that.$emit('clearMenus')
                });
            }
        }
    }
}
